import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import './index.css'
import {BrowserRouter as Router, Route} from 'react-router-dom'

const Root = () => {
  return (
    <Router>
      <div>
        <Route path='/' component={App} />
      </div>
    </Router>
  )
}

ReactDOM.render(
  <Root />,
  document.getElementById('root')
)



